<template>
   <div class='root'>
      <action-bar title='预售活动'></action-bar>
      <van-tabs @change="changeTabs" animated swipeable v-model="currentTab" color="#42D18A">
         <van-tab v-for="pageItem in pageData" :key="pageItem.title" :title="pageItem.title">
            <scroll-view :noMoreData="pageItem.noMoreData" :data="pageItem.reserveList">
               <div class="sc-top"></div>
               <div class="card-box" v-for="item in pageItem.reserveList" :key="item.id">
                  <!-- 基本信息 -->
                  <div class="item-title">
                     <div>{{ item.title }}</div>
                     <div>{{ item.state }}</div>
                  </div>
                  <div class="item-content">
                     <div class="item-content-left">类型</div>
                     <div class="item-content-right">{{ item.priceModel }}</div>
                  </div>
                  <div class="item-content">
                     <div class="item-content-left">品类数量</div>
                     <div class="item-content-right">{{ item.skuCount }}</div>
                  </div>
                  <div class="item-content">
                     <div class="item-content-left">开始至结束时间</div>
                     <div class="item-content-right">{{ item.startSalesTime }} 至 {{ item.endSalesTime }}</div>
                  </div>
                  <div class="item-content">
                     <div class="item-content-left">商品起送时间段</div>
                     <div class="item-content-right">{{ item.startDeliveryDate }} 至 {{ item.endDeliveryDate }}</div>
                  </div>
                  <!-- 操作按钮 -->
                  <div class="item-operate">
                     <!-- 编辑 -->
                     <div v-if="item.editBtn != ''" class="item-edit" @click="bindUpdateDetail(item.id)">
                        {{ item.editBtn }}</div>
                     <!-- 录入尾款 -->
                     <!-- <div v-if="item.retainageBtn != ''" class="item-edit"
                        bindtap="bindRetainage">{{ item.retainageBtn }}</div> -->
                         <!-- 查看活动 -->
                     <div class="item-detail" @click="bindSeeDetail(item.id)">查看活动</div>
                  </div>
               </div>
            </scroll-view>
         </van-tab>
      </van-tabs>
   </div>
</template>
<script>
import { Tab, Tabs } from 'vant'
import { presellActivityList } from '@/http/api'
export default {
  name: 'reserveShopList',
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data () {
    return {
      currentTab: 0,
      pageData: [{
        title: '即将预售',
        start: 0, // 分页查询
        limit: 10, // 每页查询数量
        isScroll: true, // 是否允许商品列表可以滚动
        noMoreData: false, // 没有更多数据了
        reserveList: [], // 列表数据
        isReset: true, // 是否重置页面
        triggered: false // 下拉刷新是否被触发
      },
      {
        title: '预售中',
        start: 0,
        limit: 10,
        isScroll: true,
        noMoreData: false,
        reserveList: [],
        isReset: true,
        triggered: false
      },
      {
        title: '已结束预售',
        start: 0,
        limit: 10,
        isScroll: true,
        noMoreData: false,
        reserveList: [],
        isReset: true,
        triggered: false
      }
      ]
    }
  },
  methods: {
    // 查看活动
    bindSeeDetail (id) {
      this.$router.push({
        name: 'reserveDetail',
        query: {
          id
        }
      })
    },
    // 录入尾款
    bindRetainage (id) {
      console.log('录入尾款')
    },
    // 编辑
    bindUpdateDetail (id) {
      this.$router.push({
        path: '/editReserveShop',
        query: {
          id
        }
      })
    },
    changeTabs () {
      this.pageData[this.currentTab].isReset = true
      this.getList()
    },
    getList () {
      const pageData = this.pageData
      const page = pageData[this.currentTab]
      if (page.isReset) {
        page.start = 0
      }
      const data = {
        state: page.title,
        priceModel: '',
        start: page.start,
        limit: page.limit
      }
      presellActivityList(data).then(res => {
        // 装载数据
        let nmd = false
        if (res.data.list.length < 10) {
          nmd = true
        }
        page.noMoreData = nmd
        page.isScroll = true
        if (page.reserveList === [] || page.isReset === true) {
          page.reserveList = res.data.list
        } else {
          page.reserveList.push(...res.data.list)
        }
        page.isReset = false
        this.pageData = pageData
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
    this.getList()
  }
}
</script>
<style lang="scss" scoped>

@import '@/utils/tabScroll.css';

.root {
   width: 100vw;
   height: 100vh;
   display: flex;
   flex-direction: column;
}

.container {
   flex-grow: 1;
   position: relative;
}

.content {
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
}

.card-box {
   margin: .375rem;
   margin-top: 0;
   background-color: white;
   padding: .375rem;
   border-radius: .3125rem;
}

.sc-top{
   height:.375rem;
}

.item-title {
   color: #42D18A;
   font-size: 1rem;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
}

.item-content {
   width: 100%;
   display: flex;
   flex-direction: row;
   padding: .5rem 0;
   font-size: .875rem;
}

.item-content-left {
   width: 36%;
}

.item-content-right {
   width: 64%;
}

.item-operate {
   display: flex;
   flex-direction: row;
   justify-content: flex-end;
   margin-top: .5rem;
}

.item-edit {
   border: .0625rem solid #42D18A;
   min-width: 4.375rem;
   padding: .375rem;
   border-radius: .3125rem;
   background-color: white;
   text-align: center;
   font-size: .875rem;
}

.item-detail {
   border: .0625rem solid #42D18A;
   min-width: 4.375rem;
   padding:.375rem;
   border-radius: .3125rem;
   margin-left: .5rem;
   background-color: #42D18A;
   text-align: center;
   color: white;
   font-size: .875rem;
}
</style>
